h1 ,h2, h3, h4, p, a{
    font-family: Aproxima-nova, "Catamaran", "Helvetica Neue", "Helvetica", "Arial", sans-serif;;
    z-index: 2;
}

ul{
    padding: 0;
}

li, input{
    list-style: none;
    text-align: center;
    border: none;
    outline: none;
}


span {
    display: block;
}



button{
    z-index: 3;
    outline: none;
    border: none;
}