


.section1{
    @include dimensions($primeGreen, 100%, 650px, 0, 0);

    h1{
        @include fontSettings($pureWhite, 38px, 400, 1, 80px 0 0);
    }

    .seal{
        @include dimensions($primeGreen, 200px, 200px, 0, 50px auto 0);
    }

    p{
        @include fontSettings($pureWhite, 18px, 300, 1.5, 18px 0);
    }
}

.section2{
    @include dimensions($lightGrey, 100%, 585px, 0, 0);

    .services{
        @include dimensions($pureWhite, 80%, 152px, 5px, -50px auto 55px);

        .svg-temp{
            @include dimensions($primeGreen, 72px, 72px, 36px, 20px auto 10px);
        }

        h2{
            @include fontSettings($primeGreen, 18px, 300, 1, 10px auto);
        }
    }
    
}

.section3{
    @include dimensions($primeGreen, 100%, 500px, 0, -50px 0 0);

    h1{
        @include fontSettings($pureWhite, 38px, 400, 1, 200px 0 30px);
    }
      
    input[type=text] {
        @include fontSettings($pureBlack, 18px, 300, 1, 0 0 0 32px); 
        @include dimensions($pureWhite, 80%, 30px, 0 15px 15px 0, 0 0 0 32px);
    }

    .search-bar button {
        @include dimensions($pureWhite, 30px, 30px, 15px, 0 0 0 5px);
    }

}









